body{

    margin-top: 0;
    margin-left: 0;
    margin-right: 0;
}

.hi{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    padding: 15px;
    
}


.calculator{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-color: white;
    width: 50vw;
    height: 70vh;
    border-radius: 20px;


}

.button-wraper{
    width: 10vw;
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    background-color: #5a5a5a;
    color: aliceblue;
    flex: 1;
    border-radius: 60px;
    
}
.buts{
    display: flex;
    gap: 10px;
    margin-top: 2px;
    

}
.display{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    height: 25vh;
    border: 1px solid yellow;
    width: 45vw;
    color: black;
    background-color: rgb(174, 174, 220);
    padding-right: 10px;
    border-radius: 10%;
}

.sym{
    display: flex;
    justify-content: center;
    align-items: center;

    width: 90%;
    height: 90%;
}


